body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{color:#222222}a:hover{text-decoration:none}

.coin-info-rechart{margin:10px 0}.coin-info-rechart.fetching,.coin-info-rechart.error{background:#f8f8f8}.coin-info-rechart__wrapper.fetching,.coin-info-rechart__wrapper.error{min-height:300px;display:flex;justify-content:center;align-items:center;background-size:contain}.coin-info-rechart__content{border:1px solid;border-color:#fff #dee2e6 #dee2e6;padding:5px;overflow:hidden}.coin-info-rechart__content .customized-tooltip-content{background:#fff;border:2px solid #e2e2e2;padding:15px}.coin-info-rechart__content .customized-tooltip-content p{margin:0}.coin-info-rechart__content .customized-tooltip-content ul{list-style-type:none;margin:0;padding:0}.coin-info-rechart__controll .nav-link{border-radius:5px 5px 0 0}@media (max-width: 800px){.coin-info-rechart__controll .nav-item{width:25%}.coin-info-rechart__controll .nav-link{width:100%;font-size:0.8rem;margin-right:10px;border-radius:5px}.coin-info-rechart__controll .nav-link.active{border:1px solid #00d665}.coin-info-rechart__content{overflow:auto}}

.coin-info-detail-info__head-info{margin:10px 0;display:flex;justify-content:space-between;flex-direction:row}.coin-info-detail-info__head-info .item .title{font-weight:500;color:#b1b1b1;font-size:0.9rem}@media (max-width: 800px){.coin-info-detail-info__head-info{flex-direction:column}.coin-info-detail-info__head-info .item{padding:5px 0}.coin-info-detail-info__head-info .item .text{margin-left:10px}}

.coin-info-head__logo img{width:80px;margin-right:20px}.coin-info-head__wrapper{display:flex;align-items:center}.coin-info-head__price-text{font-size:1.8rem;margin-right:10px}.coin-info-head__price-change{font-size:1.3rem;display:inline;color:#26da71}.coin-info-head__price-change--is-down{color:#ff7060}.coin-info-head__price-change-arrow{vertical-align:super;font-size:1.6rem}@media (max-width: 800px){.coin-info-head__logo img{width:60px}.coin-info-head__price-text{font-size:1.6rem}.coin-info-head__price-change{display:block;font-size:1.4rem}}

.converter-field .form-control{width:40%}.converter-field .custom-select{font-size:1rem}

.header{padding:10px 0;margin-bottom:20px;border-bottom:1px solid #00d665}.header-logo{vertical-align:sub}.header-logo img{height:25px}.header-menu{text-align:right}.header-menu__item{display:inline-block}.header-menu__item+.header-menu__item{margin-left:20px}

input[type="number"]::-webkit-outer-spin-button,input[type="number"]::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type="number"]{-moz-appearance:textfield}input[type="number"]:hover,input[type="number"]:focus{-moz-appearance:number-input}input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}

.loader{color:#00d665}

.top-coins-table td div{padding:5px}.top-coins-table td .price-down{background:#ff7060}.top-coins-table td .price-up{background:#26da71}

.search-field{display:inline-flex;align-items:center;width:200px;margin-right:25px}.search-field input{height:30px;border:none;border-bottom:1px solid #34df7c;border-radius:0;margin-right:9px}.search-field input:focus{outline:none !important}.search-field button{border:none;border-radius:0;background:transparent;outline:none;padding:5px}

.coin-info__title small{font-weight:500;color:#7e7e7e}

.currency-converter{border:1px solid #eee;padding:20px;border-radius:5px}.currency-converter h5{margin:0;padding:0}

.top-coins__wrapper{overflow:hidden}.top-coins__coin-name{display:flex;align-items:center}.top-coins__coin-image{height:2rem;margin-right:10px}.top-coins__coin-text{line-height:1rem}.top-coins-table thead tr th:first-child{max-width:40px}.top-coins-table tbody tr td,.top-coins-table tbody tr th{vertical-align:inherit}.top-coins-table tbody tr td:not(:first-child),.top-coins-table tbody tr th:not(:first-child){min-width:130px}@media (max-width: 800px){.top-coins__wrapper{overflow:auto}}

